.table {
  .cellWrapper {
    display: flex;
    align-items: center;
  }

  .status {
    padding: 5px;
    border-radius: 5px;

    &.Approved {
      color: green;
      background-color: rgba(0, 128, 0, 0.151);
    }
    &.Pending {
      color: goldenrod;
      background-color: rgba(189, 189, 3, 0.103);
    }
  }
}
.dashboard-table tbody,
td,
tfoot,
th,
thead,
tr {
  border-width: 2px;
}
.deleteButton {
  text-align: center;
  padding: 10px 30px;
  border-radius: 5px;
  color: crimson;
  border: 1px dotted rgba(223, 13, 55, 0.6);
  cursor: pointer;
}
.deleteButton:hover {
  background-color: crimson;
  color: white;
  border: 1px dotted rgb(255, 245, 247);
}
.addButton {
  text-decoration: none;
  color: green;
  font-size: 16px;
  font-weight: 400;
  border: 1px dotted green;
  padding: 5px;
  border-radius: 5px;
  cursor: pointer;
}
